@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
html{--primary-color: #294B6B;--secundary-color: #0F6FFF;--tertiary-color: #17213A;--text-primary-color: #fff;--text-secundary-color: #A4A0A0;--text-tertiary-color: #087DE9}html[data-theme="dark"]{--primary-color: #fff;--secundary-color: #0F6FFF;--tertiary-color: #DAE1F4;--text-primary-color: #294B6B;--text-secundary-color: #294B6B;--text-tertiary-color: #087DE9}*{box-sizing:border-box;padding:0;margin:0}html{font-size:62.5%;font-family:'Roboto', sans-serif}.main{width:100vw;height:100vh;background-color:#17213A;background-color:var(--tertiary-color);position:relative;margin:0;display:grid;align-items:center;justify-items:center}.main .container{width:80%;max-width:450px;height:80%;max-height:410px;display:flex;flex-direction:column;justify-content:space-between;align-items:center}

html{--primary-color: #294B6B;--secundary-color: #0F6FFF;--tertiary-color: #17213A;--text-primary-color: #fff;--text-secundary-color: #A4A0A0;--text-tertiary-color: #087DE9}html[data-theme="dark"]{--primary-color: #fff;--secundary-color: #0F6FFF;--tertiary-color: #DAE1F4;--text-primary-color: #294B6B;--text-secundary-color: #294B6B;--text-tertiary-color: #087DE9}.themeComponent{width:100%;height:50px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.themeComponent-title p{color:#fff;color:var(--text-primary-color);font-weight:bold;font-size:2rem}.themeComponent-change{width:50px;display:flex;flex-direction:row;align-items:center;justify-content:space-between}input[type=checkbox]{height:0;width:0;visibility:hidden}label{cursor:pointer;text-indent:-9999px;width:50px;height:20px;background:#DAE1F4;display:block;border-radius:100px;position:relative}label:after{content:'';position:absolute;top:3px;left:5px;width:15px;height:15px;background:#17213A;border-radius:90px;transition:0.3s}input:checked+label{background:#17213A}input:checked+label:after{left:calc(100% - 5px);-webkit-transform:translateX(-100%);transform:translateX(-100%);background:#DAE1F4}label:active:after{width:10px}

html{--primary-color: #294B6B;--secundary-color: #0F6FFF;--tertiary-color: #17213A;--text-primary-color: #fff;--text-secundary-color: #A4A0A0;--text-tertiary-color: #087DE9}html[data-theme="dark"]{--primary-color: #fff;--secundary-color: #0F6FFF;--tertiary-color: #DAE1F4;--text-primary-color: #294B6B;--text-secundary-color: #294B6B;--text-tertiary-color: #087DE9}.searchC{width:100%;height:45px;background:#294B6B;background:var(--primary-color);border-radius:10px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 5px 0 10px}.searchC-lupe-icon{width:15px;height:15px;color:#0F6FFF;color:var(--secundary-color)}.searchC-input{width:60%}.searchC-input ::-webkit-input-placeholder{color:#A4A0A0;color:var(--text-secundary-color)}.searchC-input :-ms-input-placeholder{color:#A4A0A0;color:var(--text-secundary-color)}.searchC-input ::placeholder{color:#A4A0A0;color:var(--text-secundary-color)}.searchC-input input{border:none;color:#fff;color:var(--text-primary-color);font-size:1.1rem;font-weight:100;background:#294B6B;background:var(--primary-color)}.searchC-input :focus{outline:none}.searchC-button{height:70%;width:60px}.searchC-button button{cursor:pointer;width:100%;height:100%;border-radius:10px;border:none;color:#fff;color:var(--text-primary-color);background:#0F6FFF;background:var(--secundary-color);font-weight:100;font-size:1.1rem}

html{--primary-color: #294B6B;--secundary-color: #0F6FFF;--tertiary-color: #17213A;--text-primary-color: #fff;--text-secundary-color: #A4A0A0;--text-tertiary-color: #087DE9}html[data-theme="dark"]{--primary-color: #fff;--secundary-color: #0F6FFF;--tertiary-color: #DAE1F4;--text-primary-color: #294B6B;--text-secundary-color: #294B6B;--text-tertiary-color: #087DE9}.infoContainer{width:100%;height:90%;padding:15px;background:#294B6B;background:var(--primary-color);border-radius:10px;margin-top:15px}.infoContainer-container{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:100%;height:100%}.infoContainer-noSearchComponent{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.infoContainer-notFoundUser{text-align:center;position:relative;top:50%;font-size:2rem;-webkit-transform:translateY(-80%);transform:translateY(-80%);color:#fff;font-weight:700}@media screen and (min-width: 768px){.infoContainer-container{display:grid;grid-template-columns:30% 70%;grid-template-rows:50% 25% 25%}}

